import { IVPAIDElement } from "../core/IVPAIDElement.js";

export class Image extends IVPAIDElement {
  constructor(data, element, vpaidSettings) {
    super(data, element, vpaidSettings);
  }

  createElement() {
    this.createRootElement();
    let img = document.createElement("img");
    img.src = this.element.url;
    img.style = {
      width: "100%",
      height: "100%"
    };
    img.onload = () => this.markLoaded();
    this.domElement.appendChild(img);
    return this.domElement;
  }
}
